.page-footer {
  margin-top: auto;
  padding: 248px 0 98px;

  @media (max-width: $desktop-width) {
    padding: 155px 35px 52px;
  }

  @media (max-width: $tablet-width) {
    padding: 119px 0 50px;
  }
}

.page-footer__title {
  margin-bottom: 30px;

  @media (max-width: $desktop-width) {
    margin-bottom: 20px;
  }
}

.page-footer__intro {
  font-weight: 400;
  font-size: 17px;
  line-height: 31px;
  color: rgba($black,0.7);
  text-align: center;
  max-width: 555px;
  margin: 0 auto;
  margin-bottom: 42px;

  @media (max-width: $desktop-width) {
    max-width: 100%;
    margin-bottom: 20px;
  }

  @media (max-width: $tablet-width) {
    padding: 0 25px;
    margin-bottom: 25px;
  }
}
